import React from 'react'
import Card from 'react-bootstrap/Card';
import CameraAltIcon from '@mui/icons-material/CameraAlt';
import VideoCameraBackIcon from '@mui/icons-material/VideoCameraBack';
import CollectionsIcon from '@mui/icons-material/Collections';
import Button from 'react-bootstrap/Button';
import { Link } from 'react-router-dom';
import styled from 'styled-components'
import './body01.css'
const Pa=styled.p`
color: black;
    text-decoration: none;
    font-size: 15px;
`
const Iconop2=styled.div`
display: flex;
padding: 20px;
justify-content: center;
flex-direction: column;
align-items:center;
`
const Iconop=styled.div`
display: flex;
    justify-content: center;
`


export default function BodyCrad() {
  return (

    <Card className="text-center " >
    
    <Card.Body>
     <h1 style={{fontFamily: 'Rubik Distressed, cursive'}}>Our Service</h1>
      <Iconop>

     <Iconop2>


      <Link to="/Photos" className='link-text'style={{fontFamily: 'Oswald, cursive'}}>
      <CameraAltIcon style={{ fontSize: '60px'}}/>
        <Pa>Photography</Pa></Link>
     </Iconop2>
      
     <Iconop2>

      <Link to="/Videos" className='link-text'style={{fontFamily: 'Oswald, cursive'}}>
      <VideoCameraBackIcon style={{ fontSize: '60px'}}/>
        <Pa>Videography</Pa></Link>
      </Iconop2>
      <Iconop2>

      <Link to="/Albums" className='link-text'style={{fontFamily: 'Oswald, cursive'}}>
     <CollectionsIcon style={{ fontSize: '60px'}}/>
     
        <Pa>Albums</Pa></Link>
      </Iconop2>

      </Iconop>
      
      <Card.Text style={{fontFamily: 'Cedarville Cursive, cursive',fontSize:'40px'}}>
        The moment you care is the moment we capture
      </Card.Text>
      <Button variant="primary" style={{fontFamily: 'DynaPuff, cursive'}}>See Our Works</Button>
    </Card.Body>
  
  </Card>

  )
}
