import React from 'react'
import babu from '../Images/babu.jpg'
import styled from 'styled-components'
import {mobile} from '../Responsive'

const Container=styled.div`
display:flex;
${mobile({flexDirection:"column"})}
`
const InfoCont=styled.div`
margin-left:10px;
display:flex;
flex-direction:column;
${mobile({justifyContent:"center",alignItems:"center"})}
`
const Image=styled.img`
width:300px;
clip-path: circle(50%);
`
export default function About() {
  return (
   <Container>
    <Image src={babu}/>
   <InfoCont>
    <h1 style={{color:"#393939",fontFamily:"Cedarville Cursive,cursive"}}>Babu Koushik</h1>
    <span style={{color:"#767676",fontFamily:"Rubik Distressed, sans-serif"}}>Lead Photographer,Camera man, Organizer</span>
    <p style={{fontFamily:"DynaPuff, sans-serif"}}>Lorem ipsum dolor sit amet consectetur adipisicing elit. Eius debitis error sed officiis consequatur ex sequi accusantium cum laboriosam ipsam! Dolore vel, ullam cumque ipsa molestiae tempora amet necessitatibus omnis.</p>
   </InfoCont>
   </Container>
  )
}
