import React from 'react'
import styled from 'styled-components'
import { mobile } from '../Responsive';

import { Comment0 } from '../Data';

const Container=styled.div`
padding:20px;
display:flex;
flex-wrap: wrap;
    justify-content: space-between;
`
const Wrapper=styled.div`
background-color:#E7E7E7;
margin:10px;
padding:10px;
border-radius: 25px;
flex-direction: row; 
${mobile({flexDirection:"column",width:"100%"})}
`
const User0=styled.div`

`

export default function Comment() {
  return (
    <Container>
    {Comment0.map((e)=>(
   <Wrapper key={e.id}>
   
   <User0>
    {e.riv}
   </User0>
   <User0>
    <h4 style={{color:'#9B9B9B'}}>{e.username}</h4>
   </User0>
   </Wrapper>
    
    ))}
    </Container>
  )
}
